import { forwardRef } from "react"
import i18next from "i18next"
import { FormHelperText, InputLabel, OutlinedInput, Stack } from "@rp/react/web/mui/core"

type Props = { helperText?: string } & React.ComponentProps<typeof OutlinedInput>

export const LoginEmailField = forwardRef<HTMLDivElement, Props>(
  ({ defaultValue = "", helperText, ...props }, ref) => {
    return (
      <Stack width="100%">
        <InputLabel htmlFor="email">Email</InputLabel>
        <OutlinedInput
          ref={ref}
          id="email"
          defaultValue={defaultValue}
          placeholder={i18next.t("common.emailPlaceholder")}
          sx={{ width: "100%" }}
          name="email"
          required
          {...props}
        />
        {helperText !== undefined ? (
          <FormHelperText sx={theme => ({ color: theme.palette.error.main })}>
            {helperText}
          </FormHelperText>
        ) : null}
      </Stack>
    )
  },
)
